.card {
    background: #fff;
    border-radius: 2px;
    height: 325px;
    margin: 1rem;
    position: relative;
    width: 325px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }

  .card > .img-container {
    height: 100%;
    text-align: center;
  }
  .card > .img-container > img {
    width: 100%
  }
  
  .card > .content > ul > li {
    margin-top: 18px;
  }
  
  .img-thumbnail{
    padding: 2px;
    margin:0 auto;
  }

 

 


  
 
